.redirect-wrapper {
  justify-content: center;
  align-items: center;
  height: 100%;
  display: flex;
}

.redirect {
  color: #e5dada;
  text-align: center;
  flex-direction: column;
  align-items: center;
  gap: 25px;
  font-family: sans-serif;
  display: flex;
  transform: translateY(-50px);
}

.redirect--heading {
  font-size: 32px;
}

.redirect--subheading {
  font-size: 20px;
}

@media screen and (width <= 500px) {
  .redirect--heading {
    font-size: 26px;
  }

  .redirect--subheading {
    font-size: 16px;
  }
}

.redirect--link:link, .redirect--link:visited {
  color: #a0a8f3d9;
}

.redirect--link:hover, .redirect--link:active {
  color: #fff;
}

